import styled, { css } from 'styled-components';

const getSizeStyles = (size) => {
  switch (size) {
    case 'sm':
      return css`
        width: 3.4rem;
        height: 3.4rem;
        border-radius: 5px;
      `;
    case 'md':
      return css`
        width: 4.4rem;
        height: 4.4rem;
        border-radius: 5px;
      `;
    case 'lg':
      return css`
        width: 5.4rem;
        height: 5.4rem;
        border-radius: 5px;
      `;
    default:
      return css``;
  }
};

export const StyledAvatarComponent = styled.div`
  display: flex;
  gap: 1rem;
  background-color: var(--active-color);
  border-radius: 0.5rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 15px 50px;

  ${(props) => getSizeStyles(props.size)}

  .avatar-square {
    width: 3rem;
    height: 3rem;
    border-radius: 0.5rem;
    display: block;
    font-family: 'Rubik', sans-serif;
    font-size: 6px;
    margin: 0;
    overflow: clip margin content-box;
    overflow-x: clip;
    overflow-y: clip;
    padding: 0;
  }

  ${(props) =>
    props.customStyle &&
    css`
      /* Add any additional custom styles based on the prop */
      color: ${props.customStyle.textColor || 'inherit'};
      background-color: ${props.customStyle.backgroundColor ||
      'var(--active-color)'};
    `}
`;
